<template>
    <section class="section-wrap">
        <c-title title="" engTitle="Core Competence"></c-title>
        <ul class="core-list">
            <li v-for="(item, index) in coreList" :key="index">
                <div class="core-logo"><i :class="'ic icon-core-' + (index + 1)"></i></div><div class="core-content">
                    <h3>{{item.title}}</h3>
                    <p>{{item.content}}</p>
                </div>
            </li>
        </ul>
    </section>
</template>
<script>
import cTitle from './cTitle'
export default {
    data() {
        return {
            coreList: [{
                title: '',
                content: ''
            }, {
                title: '',
                content: ''
            }, {
            //     title: '',
            //     content: ' ISO9001ISO20000ISO14001'
            // }, {
                title: '',
                content: 'IT'
            }, {
                title: '',
                content: '4.0'
            }],
        }
    },
    methods: {
        
    },
    components: {
        cTitle
    }
}
</script>
<style lang="scss" scope>
@import "../../style/param";
.section-wrap {
    position: relative;
}
/* icon bgimages*/
.ic {
    display: inline-block;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    transition: all 0.3s;
    &.icon-core-1 {
        height: 84px;
        width: 83px;
        background-image: url(../../svg/1.svg);
        .core-list li:hover & {
            background-image: url(../../svg/11.svg);
        }
    }
    &.icon-core-2 {
        height: 96px;
        width: 95px;
        background-image: url(../../svg/2.svg);
        .core-list li:hover & {
            background-image: url(../../svg/22.svg);
        }
    }
    // &.icon-core-3 {
    //     height: 85px;
    //     width: 85px;
    //     background-image: url(../../svg/3.svg);
    //     .core-list li:hover & {
    //         background-image: url(../../svg/33.svg);
    //     }
    // }
    &.icon-core-3 {
        height: 96px;
        width: 83px;
        background-image: url(../../svg/4.svg);
        .core-list li:hover & {
            background-image: url(../../svg/44.svg);
        }
    }
    &.icon-core-4 {
        height: 77px;
        width: 94px;
        background-image: url(../../svg/5.svg);
        .core-list li:hover & {
            background-image: url(../../svg/55.svg);
        }
    }
}
.core-logo {
    height: 150px;
    text-align: center;
    .ic {
        vertical-align: top;
    }
}
.core-list {
    position: relative;
    margin: 0 auto;
    transition: left 0.3s;
    li {
        display: inline-block;
        margin: 1px;
        padding: 40px;
        width: calc(25% - 10px);
        white-space: normal;
        cursor: pointer;
        transition: all 0.3s;
        vertical-align: top;
        height: 410px;
        text-align: left;
        h3 {
            font-size: 2.4rem;
            font-weight: normal;
            transition: color 0.3s;
            min-height: 30px;
        }
        p {
            margin-top: 20px;
            line-height: 1.5;
        }
        &:hover {
            box-shadow: 0 0 0 1px $blue;
            background: $white;
            h3, p {
                color: $blue;
            }
        }
    }
}
@media only screen and (max-width: 768px) {
    .core-list {
        margin: 0;
        white-space: normal;
        li {
            margin: 0;
            margin-right: 30px;
            display: block;
            padding: 20px;
            margin-top: 20px;
            width: auto;
            height: auto;
            background: $white;
            box-shadow: 1px 1px 3px 0 $grey1;
            &:hover {
                box-shadow: initial;
            }
            p {
                margin-top: 15px;
                margin-bottom: 0;
                font-size: 1.2rem;
            }
            h3 {
                font-size: 1.6rem;
                color: $blue;
                min-height: initial;
            }

        }
    }
    .core-content {
        display: inline-block;
        width: calc(100% - 70px);
        vertical-align: middle;
    }
    .core-logo {
        display: inline-block;
        width: 70px;
        height: 60px;
        overflow: hidden;
        vertical-align: middle;
        .ic {
            background-size: 60% 60%;
        }
    }
}
@media only screen and (min-width: 768px) and (max-width: 1420px) {
    .core-list li {
        width: calc(50% - 20px);
        height: 360px;
        h3 {
            text-align: center;
        }
    }
}  
@media only screen and (min-width: 1330px) {
    
}
@media only screen and (min-width: 1860px) {
    
}
</style>



